    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: #ffffff ;
    font-size: 4rem;
    font-weight: bold;
    line-height: 4rem;
    padding-bottom: 1rem;
}

.Parallax-bg {
    background-color: #282c34;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
}

